@use "./utils";

@include utils.team-gradient(1105);
@include utils.team-gradient(1106);
@include utils.team-gradient(1107);
@include utils.team-gradient(1108);
@include utils.team-gradient(1109);
@include utils.team-gradient(1110);
@include utils.team-gradient(1111);
@include utils.team-gradient(1379);
@include utils.team-gradient(2954);
@include utils.team-gradient(2955);
@include utils.team-gradient(3513);
@include utils.team-gradient(3514);
@include utils.team-gradient(3515);
@include utils.team-gradient(3516);
@include utils.team-gradient(3517);

.__tab-button-group {
	height: var(--tab-btn-group-height);
	gap: var(--half-space);
	// overflow: scroll hidden;
	@include utils.flex-config(flex, null, space-between, center);
	padding-top: var(--half-space);
	@media screen and (man-width: utils.$large-desktop-min-width) {
		overflow: scroll hidden;
	}
	.__btn {
		flex: 1 0 20rem;
	}
}

.__btn {
	width: 20rem;
	height: 3.5rem;
	margin: var(--half-space) 0;
	background-color: var(--color-white);
	color: hsl(var(--color-black-hsl) / 0.5);
	border-radius: var(--full-space);
	border: 0.1rem solid hsl(var(--color-black-hsl) / 0.1);

	&.__btn--active {
		background-color: var(--color-red);
		color: var(--color-white);
		border: none;

		.__btn-text {
			font-family: utils.$font-medium;
		}
	}
}

.__btn-text {
	font: 1.6rem/2.2rem utils.$font-regular;
}

.__auction-purse {
	position: fixed;
	bottom: 0;
	left: 0;
	width: var(--window-inner-width);
	height: var(--purse-height);
	@include utils.flex-config(flex, column, center, center);
}

.__btn-toggle-purse {
	position: fixed;
	bottom: 0;
	border-radius: var(--full-space) var(--full-space) 0 0;
	margin: 0 0 0 0;
	background-color: var(--color-dark-blue);
	color: var(--color-white);
	border: none;
	transition: bottom 500ms ease-in-out;

	.__btn-text {
		font: 1.2rem/1.8rem utils.$font-bold;
		text-transform: uppercase;
	}

	&[aria-expanded="true"] {
		bottom: 9rem;

		~ .__purse-wrapper {
			bottom: 0;
		}
	}
}

.__purse-wrapper {
	position: fixed;
	width: 100%;
	height: 9rem;
	bottom: calc(9rem * -1);
	gap: 0;
	overflow: scroll hidden;
	transition: bottom 500ms ease-in-out;
	@include utils.flex-config(flex, null, flex-start, center);
}

.__team {
	width: 15rem;
	height: 100%;
	flex-shrink: 0;
	gap: var(--half-space);
	@include utils.flex-config(flex, column, center, center);
}

.__team-image {
	width: 4rem;
	height: 4rem;
	background-color: var(--color-white);
	border-radius: 4rem;
}

.__team-flag {
	display: block;
	width: 80%;
	height: 80%;
	margin: 10%;
	object-fit: contain;
}

.__team-amount {
	background: var(--color-white);
	border-radius: 4rem;
	font: 1.2rem/1.2rem utils.$font-medium;
	padding: 0.5rem;
}

.__squad {
	.__widget-head {
		position: relative;
		height: 12rem;
		flex-wrap: wrap;
	}

	.__head-icon {
		position: absolute;
		right: 0;
		bottom: var(--half-space-negative);
		z-index: var(--z-head-icon);
	}

	.__team-squad {
		width: 100%;
		flex-wrap: wrap;
		@include utils.flex-config(flex, column, null, center);
	}

	.__squad-wrapper {
		width: 100%;
		height: 8rem;
		gap: 1rem;
		// overflow: scroll hidden;
		@include utils.flex-config(flex, null, null, null);
		@media screen and (max-width: utils.$large-desktop-min-width) {
			overflow: scroll hidden;
		}
	}

	.__team {
		position: relative;
		width: 15rem;
		height: 7rem;
		margin-bottom: var(--half-space);
		justify-content: flex-end;
		border-radius: var(--half-space);

		&::after {
			position: absolute;
			content: "";
			width: 100%;
			height: 0.2rem;
			bottom: calc(var(--half-space-negative) / 2);
		}

		&::before {
			position: absolute;
			content: "";
			bottom: var(--half-space-negative);
			border: 0.5rem solid transparent;
			border-bottom: 0;
		}

		&--active {
			&::before {
				border-top-color: var(--color-red);
			}

			&::after {
				background-color: var(--color-red);
			}
		}
	}

	.__team-image {
		position: absolute;
		top: 0;
	}

	.__team-info {
		width: 100%;
		height: 6rem;
		padding-top: 3rem;
		border-radius: var(--half-space);
		@include utils.flex-config(flex, null, center, center);
	}

	.__team-data {
		max-width: 10rem;
		font: 1.2rem/1.2rem utils.$font-medium;
		color: var(--color-white);
	}

	.__team-wrapper {
		width: 100%;
		height: 3rem;
		// padding: 0 calc(var(--full-space) * 2);

		// overflow: scroll hidden;
		@include utils.flex-config(flex, null, space-between, center);
		@media screen and (man-width: utils.$large-desktop-min-width) {
			overflow: scroll hidden;
		}
	}

	.__team-track {
		flex: 1 0 10rem;
		height: 100%;
		gap: var(--half-space);
		@include utils.flex-config(flex, null, center, center);
		&:first-child{
			flex: 1 0 20rem;
			justify-content: left;
		}
		&:nth-child(2){
			flex: 1 0 15rem;
		}
		&:nth-child(3){
			flex: 1 0 15rem;
		}
	}

	.__text {
		font: 1.4rem/1.4rem utils.$font-bold;
	}

	.__title {
		font: 1rem/1rem utils.$font-regular;
		text-transform: uppercase;
	}

	.__value {
		width: max-content;
		min-width: 6rem;
		height: 3rem;
		padding: 0.5em 1em;
		background-color: var(--color-white);
		border-radius: var(--full-space);
		font: 1.5rem/1.5rem utils.$font-bold;
		text-align: center;
	}
}

@media screen and (min-width: utils.$large-desktop-min-width) {
	.__purse-wrapper {
		overflow: hidden;
	}

	.__team {
		width: auto;
		flex: 1;
		cursor: pointer;
	}

	.__squad {
		.__widget-head {
			height: 14rem;
		}

		.__team-wrapper {
			height: 5rem;
		}

		.__text {
			font: 2rem/2rem utils.$font-bold;
		}

		.__title {
			font: 1.2rem/1.2rem utils.$font-regular;
		}
	}
}
