@use "./utils";

.__auction--live {
	.__auction-status {
		display: none;
	}

	.__current-bid {
		display: block;
	}
}

.__header {
	position: fixed;
	top: 0;
	left: 0;
	width: var(--header-width);
	height: var(--header-height);
	padding: var(--half-space) var(--full-space);
	background-color: var(--color-dark-blue);
	z-index: var(--z-header);
	@include utils.flex-config(flex, null, null, center);

	&-logo {
		width: var(--logo-width);
		height: var(--logo-height);

		.__brand-logo {
			width: 100%;
			height: 100%;
		}
	}

	&-tournament {
		width: calc(
			var(--window-inner-width) - (var(--logo-width) + var(--full-space))
		);
		margin: 0 var(--half-space);
		border-left: 0.2rem solid var(--color-white);

		.__tournament-name {
			margin-left: var(--half-space);
			font: 1.2rem / 1.8rem utils.$font-bold;
			text-transform: uppercase;
			color: var(--color-white);
			@include utils.truncate-vertical(2, auto, 1.8rem, 3.6rem);
		}
	}
	.si-Logout{
      position: relative;
	  margin-right: 3rem;
	  margin-top: -3rem;
	  cursor: pointer;
	  &:after{
		content: "\e913";
		position: absolute;
		font-family: 'sports18' !important;
		font-size: 3rem;
		color:#fff;
		cursor: pointer;
	  }
	}
	.si-back{
		// position: relative;
		cursor: pointer;
		background:url("/static-assets/images/svg/icon-back.png") ;
		background-size: cover;
		width: 4%;
    	height: 100%;
	}
}

.__auction-status,
.__current-bid {
	width: calc(var(--window-inner-width) - var(--full-space));
	margin-top: var(--half-space);
	border-radius: var(--full-radius);
	background: url("/static-assets/images/svg/current-bid.svg") center center
		no-repeat;
	background-size: cover;
	background-color: var(--color-light-grey);
}

.__auction-status {
	display: block;
	height: var(--upcoming-height);
	padding: var(--full-space);
}

.__current-bid {
	display: none;
	height: var(--current-bid-height);
}

.__status-wrapper {
	width: 100%;
	height: 100%;
	@include utils.flex-config(flex, null, center, center);
}

.__status-text {
	font: 1.4rem / calc(1.4rem * 1.5) utils.$font-bold;
	text-transform: uppercase;
	color: var(--color-dark-blue);
}

.__bid-player {
	position: relative;
	width: 100%;
	height: 12rem;
	padding: var(--half-space);
	@include utils.flex-config(flex, null, flex-start, center);

	&::before {
		position: absolute;
		content: "";
		width: 10rem;
		height: calc(10rem * 0.63793);
		top: 6rem;
		right: 0;
	}
}

.__player--sold {
	.__bid-player {
		&::before {
			background: url("/static-assets/images/svg/sold.svg");
		}
	}
}
.__player--rtm {
	.__bid-player {
		&::before {
			background: url("/static-assets/images/svg/rtm.svg");
			background-size: contain;
			background-repeat: no-repeat;
		}
	}
}
.__player--unsold {
	.__bid-player {
		&::before {
			background: url("/static-assets/images/svg/unsold.svg");
		}
	}
}

.__player-image {
	flex-basis: 10rem;
	width: 10rem;
	height: 12rem;
}

.__player-photo {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.__player-info {
	flex-basis: calc(100% - 10rem) !important;
	width: calc(100% - 10rem) !important;
	height: 12rem;
	color: var(--color-dark-blue);
	@include utils.flex-config(flex, row, center, center);
	@media screen and (min-width: utils.$xl-desktop-min-width) {
		flex-basis: calc(100% - 10rem) !important;
	width: calc(100% - 10rem) !important;

	}
	@media screen and (max-width: utils.$large-desktop-min-width) {
		flex-basis: calc(100% - 75rem) !important;
		width: calc(100% - 75rem) !important;

	}
	.si-Left{
		flex:0 0 52%;
		width:52%;
		position: relative;
		@media screen and (min-width: utils.$desktop-max-width) {
			flex:0 0 52%;
			width:52%;
	
		}
		&:after{
			content:"";
			border-right:2px solid #002F4F;
			height:100%;
		
			right: 17px;
			position: absolute;
			top: 0;
		
		}
	}
	.si-Right{
		flex:0 0 50%;
		width:50%;
		
		@include utils.flex-config(flex, column, flex-start, flex-start);
		.si-Tname{
			font: 1.4rem / calc(1.4rem * 1.5) utils.$font-bold;
			display: block;
		}
	 }
}

.__player-skill {
	@include utils.flex-config(flex, null, null, center);
}


.__skill,
.__price,
.__team-name,.__prvteam,.__rtm {
	position: relative;
	font: 1.4rem / calc(1.2rem * 1.5) utils.$font-regular;
}
.__prvteam{
	display:block;
}

.__skill {
	padding-left: var(--full-space);
	&-1 {
		&::before {
			position: absolute;
			content: "";
			width: 1.7rem;
            height: 1.7rem;
			top: 0;
			left: 0;
			background: url("/static-assets/images/svg/skill-1.svg") center center
				no-repeat;
		}
	}
	&-2 {
		&::before {
			position: absolute;
			content: "";
			width: 1.7rem;
            height: 1.7rem;
			top: 0;
			left: 0;
			background: url("/static-assets/images/svg/skill-2.svg") center center
				no-repeat;
		}
	}
	&-3 {
		&::before {
			position: absolute;
			content: "";
			width: 1.7rem;
            height: 1.7rem;
			top: 0;
			left: 0;
			background: url("/static-assets/images/svg/skill-3.svg") center center
				no-repeat;
		}
	}&-4 {
		&::before {
			position: absolute;
			content: "";
			width: 1.7rem;
            height: 1.7rem;
			top: 0;
			left: 0;
			background: url("/static-assets/images/svg/skill-4.svg") center center
				no-repeat;
		}
	}
}
.__player-PreviousTeam{
	@include utils.flex-config(flex, row, flex-start, flex-start);
}
.__name {
	font: 2.4rem/3rem utils.$font-bold;
	text-transform: uppercase;
	@include utils.truncate-vertical(2, auto, 3rem, 6rem);
}
.__player-PreviousTeam{
	@include utils.flex-config(flex, row, center, center);

}
.__bid-wrapper {
	width: 100%;
	height: 8rem;
	padding: var(--half-space) 0;
	overflow: scroll hidden;
	@include utils.flex-config(flex, null, flex-start, center);
}

.__bid {
	flex: 1 0 24rem;
	width: 24rem;
	height: 6rem;
	margin: var(--half-space);
	@include utils.flex-config(flex, null, center, center);

	&-team {
		width: 6rem;
		height: 6rem;
		@include utils.flex-config(flex, column, flex-start, center);

		&-logo {
			width: 4rem;
			height: 4rem;
		}
	}

	.__team-logo {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.__bid-info {
	// width: 20rem;
	width:18rem;
	height: 6rem;
	background-color: var(--color-grey);
	border-radius: 6rem;
	@include utils.flex-config(flex, null, null, center);
}

.__bid-icon {
	width: 7rem;
	height: 6rem;
	border-radius: 6rem;
	@include utils.linear-gradient(
		90deg,
		var(--color-grey) 0%,
		var(--color-light-grey) 46.5%,
		var(--color-grey) 46.6%,
		var(--color-light-grey) 100%
	);
}

.__bid-current {
	.__bid-info {
		background-color: var(--color-green);
	}

	.__bid-icon {
		@include utils.linear-gradient(
			90deg,
			var(--color-green) 0%,
			var(--color-light-green) 46.5%,
			var(--color-green) 46.6%,
			var(--color-light-green) 100%
		);
	}
}

.__icon-bid {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	@include utils.flex-config(flex, null, center, center);

	&::before {
		position: absolute;
		content: "";
		width: 3.5rem;
		height: 3.5rem;
		background: url("/static-assets/images/svg/icon-hammer.svg") center center
			no-repeat;
	}
}

.__bid-data {
	width: 14rem;
	height: 6rem;
	padding: var(--half-space);
}

.__bid-text {
	font: 1rem/1rem utils.$font-regular;
	color: var(--color-white);
	text-transform: uppercase;
}

.__bid-value {
	font: 2.4rem / calc(2.4rem * 1.5) utils.$font-bold;
	color: var(--color-white);
}

@media screen and (min-width: utils.$tablet-min-width) {
	.__bid-wrapper {
		overflow: hidden;
	}

	.__bid-player {
		&::before {
			top: 0;
			right: 0;
			width: 16rem;
			height: calc(16rem * 0.63793);
		}
	}
}

@media screen and (min-width: utils.$large-desktop-min-width) {
	.__auction--live {
		.__current-bid {
			@include utils.flex-config(flex, null, space-between, center);
		}
	}

	.__current-bid {
		height: var(--masthead-height);
		padding: 0 var(--half-space);
	}

	.__bid {
		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	.__bid-player {
		width: calc(100% - 82rem);

		&::before {
			top: 1rem;
			// right: 1rem;
			right:-6rem;
		}
	}

	.__player-name {
		// width: calc(100% - 16rem);
		width: calc(100% - 2.7rem);
	}

	.__bid-wrapper {
		// width: 82rem;
		width:77rem;
	}
}
