@use "./utils";

.si-cover {
    @include utils.flex-config(flex, row, center, center);
    margin-top: 12rem;
}

.si-sport {
    width: 22rem;
    height: 14.5rem;
    margin-left: 2rem;

    &:nth-child(1) {
        margin-left: 0;
    }

    @include utils.flex-config(flex, row, center, center);

    justify-content: center;
    align-items: center;
    border: 1px solid #dbe1e8;
    border-radius: var(--full-radius);
    cursor: pointer;
    position: relative;

    &:hover,
    &.active {

        border: 2px solid rgba(0, 47, 79, 0.57);
        box-shadow: 0px 0px 40px rgba(0, 47, 79, 0.3);

        .si-stxt {
            color: rgba(#002F4F, 1);
        }

        &.si-cricket {
            &:after {
                content: "\e905";
                color: #002F4F;
            }
        }




        &.si-Kabaddi {
            &:after {
                content: "\e90a";
                color: #002F4F;
            }
        }

        &.si-khokho {
            &:after {
                content: "\e90b";
                color: #002F4F;
            }
        }
    }

    &.si-cricket {
        &:after {
            content: "\e905";
            color: #7c8097;
            font-size: 10.4rem;
            position: absolute;
            font-family: 'sports18' !important;
        }
    }

    &.si-khokho {
        &:after {
            content: "\e90b";
            font-size: 15.4rem;
            position: absolute;
            font-family: 'sports18' !important;
            color: #71788d;

        }
    }



    &.si-Kabaddi {
        &:after {
            content: "\e90a";
            color: #71788d;
            font-size: 15.4rem;
            position: absolute;
            font-family: 'sports18' !important;
        }
    }

    .si-stxt {
        color: rgba(#002F4F, 0.5);


        font: 2.2rem utils.$font-regular;
        position: absolute;
        top: -4rem;
        // font-size: 24px;
    }

   
}
.si-dropdown {
    @include utils.flex-config(flex, row, center, center);
    margin-top: 6rem;
    outline:none;
    cursor: pointer;

    & select{
    border:0;
    border-radius: var(--half-radius);
    width: 29rem;
    height: 4rem;
    padding: 0 1rem;
    cursor: pointer;
     &:focus-visible{
        outline:none;
        border:0;
     }
    }
}