@use "./utils";

body {
	padding-top: var(--header-height);
	background-color: var(--color-page-background);
	position:relative;
}
.__login{
	background-color:var(--color-dark-blue);
	overflow: hidden;
	padding-top: 8vw;
	position: absolute;
    width: 100%;
    top: 0;
    height: 100vh;
}
.si-errorMsg{
	color:red;
	padding-top:0.5rem;
	text-transform:capitalize;
}
.__widget {
	position: relative;
	display: block;
	padding-top: var(--half-space);
	padding-bottom: var(--half-space);
}

.__auction-data {
	padding-bottom: calc(var(--purse-height) + 2rem);
}

.__head-title {
	position: relative;
	font: 1.4rem/1.4rem utils.$font-medium;
	text-transform: uppercase;
	color: var(--color-dark-blue) !important;
}



.__live-bids,
.__live-misc {
	.__table-col {
		flex: 1 0 65%;

		&:last-child {
			flex: 1 0 35%;
			justify-content: center;
		}
	}
}

.__widget-head {
	width: 100%;
	height: 2.5rem;
	@include utils.flex-config(flex, null, space-between, center);
}

.__head-wrap {
	width: 100%;
	gap: var(--half-space);
	flex-wrap: wrap;
	@include utils.flex-config(flex, null, center, center);
}

.__search,
.__filter {
	flex: 1 1 100%;
	width: 100%;
}

.__filter,
.__filter-wrap form {
	@include utils.flex-config(flex, null, space-between, center);
}

.__filter {
	margin-bottom: var(--half-space);
	background-color: var(--color-white);
	border-radius: var(--full-space);
}

.__filter-icon {
	position: relative;
	width: 5rem;
	height: 3rem;
	border-radius: var(--full-space);
	background-color: var(--color-red);
	@include utils.flex-config(flex, null, center, center);

	&::before {
		position: absolute;
		content: "";
		width: 2rem;
		height: calc(2rem * (3 / 5));
		background: url("/static-assets/images/svg/icon-filter.svg") center center
			no-repeat;
	}
}

.__filter-wrap {
	width: calc(100% - 5rem);
	height: 3rem;

	form {
		width: 100%;
		height: 100%;
		// gap: var(--half-space);
		overflow: scroll hidden;
	}

	.__form-group {
		position: relative;
		flex: 1 0 10rem;
		padding: 0 var(--half-space);
		margin-right: calc(var(--half-space) / 2);
		text-align: center;

		&::before {
			position: absolute;
			content: "";
			width: 0.1rem;
			height: 1.4rem;
			top: 0.8rem;
			right: calc(var(--half-space) * -1);
			background-color: var(--color-grey);
		}

		&:last-child {
			&::before {
				display: none;
			}
		}
	}

	select {
		&.__form-control {
			width: 100%;
			height: 3rem;
			font: 1.2rem/1.2rem utils.$font-regular;
			color: var(--color-grey);
			text-align: center;
			outline: none;
			border: none;
			background-color: transparent;
		}
	}
}

.__completed,
.__upcoming {
	.__filter-wrap {
		.__form-group {
			&:last-child {
				flex: 1 1 10rem;
				 padding:0;
					 margin:0;
				&:hover,&.active{
					background-color: var(--color-red);
					width:100%;
					border-top-right-radius: 10rem;
					border-bottom-right-radius: 10rem;
					.__btn{
						background-color:transparent;
						color:#fff;
						cursor: pointer;
						width:100%;
					}
				 }
			}
		}

		.__btn {
			width: auto;
			border: none;
			border-radius: 0;
            
			.__btn-text {
				font: 1.2rem/1.2rem utils.$font-regular;
			}
		}
	}
}

.__fieldset-search {
	position: relative;
	width: 100%;
	height: 4rem;
	@include utils.flex-config(flex, null, space-between, center);

	.__btn {
		width: 5rem;
		height: 4rem;
		margin: 0;
		border: none;
		border-radius: 0 var(--full-space) var(--full-space) 0;
	}

	.__btn-text {
		position: relative;
		font-size: 0;
		@include utils.flex-config(flex, null, center, center);

		&::before {
			position: absolute;
			content: "";
			width: 3rem;
			height: 2rem;
			background: url("/static-assets/images/svg/icon-search.svg") center center
				no-repeat;
		}
		&.si-close{
			&::before {
				position: absolute;
				content: "";
				width: 3rem;
				height: 2rem;
				background: url("/static-assets/images/svg/clsoe.svg") center center
					no-repeat;
			}
		}
	}
}

.__form-control {
	&[type="text"] {
		width: calc(100% - 5rem);
		height: 4rem;
		padding: calc(var(--half-space) / 2) var(--half-space);
		border-radius: var(--full-space) 0 0 var(--full-space);
		outline: none;
		border: none;
	}
}

.__widget-body {
	width: 100%;
	height: calc(100% - 2.5rem);
}

.__widget-links {
	text-align: right;
	width:5rem;
	height:2rem;
	cursor: pointer;
	position: absolute;
    right: 1rem;
	// z-index: 99;
}

.__more-link {
	font: 1rem/1rem utils.$font-regular;
	color: var(--color-red);
	cursor: pointer;
	text-align: right;
	width:5rem;
	height:2rem;
}

.__widget-table {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: var(--full-radius);
	background-color: var(--color-white);
	overflow: scroll hidden;
}

.__table-head {
	position: sticky;
	width: 100%;
	height: 3rem;
	// padding: 0 var(--half-space);
	top: 0;
	color: var(--color-white);
	border-radius: var(--full-radius) var(--full-radius) 0 0;
	// background-color: var(--color-dark-blue);
	z-index: var(--z-table-header);
}

.__head-row {
	width: 100%;
	height: 3rem;
	@include utils.flex-config(flex, null, null, null);

	.__table-col {
		background-color: var(--color-dark-blue);
		border: none;
	}

	.__col-text {
		font-family: utils.$font-bold;
	}
}

.__table-body {
	width: 100%;
	height: calc(100% - 3rem);

	.__table-row {
		height: 4rem;
		@include utils.flex-config(flex, null, null, null);
	}
}

.__col-text {
	position: relative;
	font: 1.4rem/1.4rem utils.$font-regular;
	@include utils.truncate-vertical(2, auto, 1.2rem, 2.8rem);
}

.__table-row {
	width: 100%;

	&:last-child {
		border: none;

		.__table-col {
			border: none;
		}
	}
}

.__table-col {
	width: 100%;
	height: 100%;
	padding: 0 var(--half-space);
	border-bottom: 0.1rem solid hsl(var(--color-black-hsl) / 0.2);
	@include utils.flex-config(flex, null, flex-start, center);

	&:last-child {
		justify-content: flex-start;
	}
}

.__head-icon {
	position: relative;
	width: 3rem;
	height: 3rem;
	border-radius: var(--full-space);
	background-color: var(--color-white);

	.__head-link {
		display: block;
		width: 100%;
		height: 100%;

		.__btn-text {
			position: relative;
			width: 100%;
			height: 100%;
			font-size: 0;

			@include utils.flex-config(flex, null, center, center);

			&::after {
				position: absolute;
				content: "";
				width: 2rem;
				height: 2rem;
				bottom: 0.4rem;
				right: 0.6rem;
				background: url("/static-assets/images/icon-excel.png") center center
					no-repeat;
				background-size: contain;
			}
		}
	}
}

.__live-bids {
	.__head-row,
	.__table-row {
		// display: grid;
		// grid-template-columns: repeat(6, 1fr);
		// align-items: center;
		@include utils.flex-config(null, null, space-between, center);
	}

	/* .__table-col {
		&:first-child {
			grid-column: span 4;
		}
		&:last-child {
			grid-column: span 2;
		}
	} */

	.__table-body {
		.__table-col {
			&:last-child {
				padding-right: var(--full-space);
				justify-content: center;
			}
		}
	}

	.__table-row {
		position: relative;

		&::after {
			position: absolute;
			content: "";
			width: var(--half-space);
			height: 100%;
			top: 0;
			right: 0;
		}

		&:nth-child(1) {
			&::after {
				background-color: hsl(var(--color-red-hsl) / 1);
			}
		}

		&:nth-child(2) {
			&::after {
				background-color: hsl(var(--color-red-hsl) / 0.8);
			}
		}

		&:nth-child(3) {
			&::after {
				background-color: hsl(var(--color-red-hsl) / 0.6);
			}
		}

		&:nth-child(4) {
			&::after {
				background-color: hsl(var(--color-red-hsl) / 0.4);
			}
		}

		&:nth-child(5) {
			&::after {
				background-color: hsl(var(--color-red-hsl) / 0.2);
			}
		}
	}
}

.__live-top-buys {
	.__head-row,
	.__table-row {
		// display: grid;
		// grid-template-columns: repeat(12, minmax(4rem, 1fr));
		// align-items: center;
		@include utils.flex-config(null, null, null, center);
	}

	.__table-col {
		&:first-child {
			flex: 1 0 15rem;
			// grid-column: span 4;
		}
		&:nth-child(2) {
			flex: 1 0 12rem;
			// grid-column: span 4;
		}
		&:nth-child(3) {
			flex: 1 0 18rem;
			// grid-column: span 4;
		}
		&:last-child {
			flex: 1 0 8rem;
			// grid-column: span 2;
		}
	}
}

.__live-misc {
	.__head-row,
	.__table-row {
		// display: grid;
		// grid-template-columns: repeat(6, 1fr);
		// align-items: center;
		@include utils.flex-config(null, null, space-between, center);
	}

	/* .__table-col {
		&:first-child {
			grid-column: span 4;
		}
		&:last-child {
			grid-column: span 2;
		}
	} */
}

.__completed,
.__upcoming {
	.__widget-head {
		height: auto;
		flex-direction: column;
		align-items: flex-end;
	}

	.__head-row,
	.__table-row {
		// display: grid;
		// grid-template-columns: repeat(9, minmax(8rem, 1fr));
		@include utils.flex-config(flex, null, space-between, center);
	}

	.__table-col {
		justify-content: left;

		&:last-child {
			justify-content: left;
		}

		&:nth-child(2) {
			justify-content: left;
			cursor: pointer;

			.__col-text {
				width: auto;
				text-align: left;
				padding-right: var(--half-space);
			}
		}

		&:nth-child(2) {
			.__table-team-logo {
				background-color: var(--color-white);
				border-radius: 100%;
				box-shadow: 0 0 0.4rem hsl(var(--color-black-hsl) / 0.75);
			}
		}

		.__col-text {
			width: auto;
			text-align: center;
		}
	}

	.__table-body {
		.__table-row {
			height: 5rem;
		}
		.__table-col {
			&:nth-child(5),
			&:nth-child(8) {
				background-color: hsl(var(--color-red-hsl) / 0.1);
			}
		}
	}

	.__table-team-logo {
		width: 4rem;
		height: 4rem;
		// box-shadow: 0 0 0.4rem hsl(var(--color-black-hsl) / 0.75);
	}
}

.__completed {
	.__table-col {
		flex: 1 0 8rem;

		&:first-child {
			flex: 1 0 6rem;
			max-width: 6rem;
		}

		&:nth-child(2),
		&:nth-child(8) {
			flex: 1 0 20rem;
			// grid-column: span 2;
		}
		&:nth-child(4),
		&:nth-child(5) {
			justify-content: center;
		}
	}
}

.__upcoming {
	.__table-col {
		flex: 1 0 10rem;
		&:last-child{
			justify-content: center;
		}
		&:first-child,
		&:nth-child(2) {
			flex: 1 0 18rem;
		}
	}
	.__table-body {
		.__table-row {
			height: 5rem;
		}
		.__table-col {
			&:nth-child(5) {
				background-color:transparent;
			}
			&:nth-child(6) {
				background-color: hsl(var(--color-red-hsl) / 0.1);
			}
		}
	}
}

.__squad {
	.__table-col {
		flex: 1 0 12rem;
		justify-content: center;

		&:first-child {
			flex: 1 0 16rem;
			justify-content: left;
		}

		&:nth-child(2) {
			justify-content: left;
		}
	}

	.__table-body {
		.__table-col {
			&:nth-child(3) {
				background-color: hsl(var(--color-dark-blue-hsl) / 0.1);
			}

			&:nth-child(4) {
				background-color: hsl(var(--color-red-hsl) / 0.1);

				.__col-text {
					color: var(--color-red);
					font-family: utils.$font-bold;
				}
			}
		}
	}
}

.__col-sort {
	width: 2rem;
	height: 3rem;
	padding-left: var(--half-space);
	cursor: pointer;
	@include utils.flex-config(flex, column, space-evenly, center);
}

.__icon-sort {
	position: relative;
	width: 1rem;
	height: 1rem;

	&::before {
		position: absolute;
		content: "";
	}

	&-up {
		&::before {
			top: 0.5rem;
			left: 0;
			border: 0.5rem solid transparent;
			border-top: 0;
			border-bottom-color: var(--color-white);
		}

		&.__icon-sort--active {
			&::before {
				border-bottom-color: var(--color-red);
			}
		}
	}

	&-down {
		&::before {
			bottom: 0.5rem;
			left: 0;
			border: 0.5rem solid transparent;
			border-bottom: 0;
			border-top-color: var(--color-white);
		}

		&.__icon-sort--active {
			&::before {
				border-top-color: var(--color-red);
			}
		}
	}
}

.__table-team-logo {
	width: 2rem;
	height: 2rem;
	margin-right: calc(var(--half-space) / 2);
	object-fit: cover;
}

.__auction-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: var(--window-inner-width);
	height: var(--window-inner-height);
	background: hsl(var(--color-black-hsl) / 0.9);
	z-index: var(--z-modal);
	@include utils.flex-config(none, null, center, center);

	&--active {
		display: flex;
	}
.__live-bids {
	.__head-title {
	color:red !important;
	}
}
	.__widget {
		width: 80%;
		height: calc(var(--window-inner-height) - 10rem);
	}

	.__btn {
		width: 6rem;
		height: 2rem;
		margin: 0;
		background-color: var(--color-dark-blue);
		color: var(--color-white);

		.__btn-text {
			font: 1.2rem/1.2rem utils.$font-regular;
		}
	}

	.__widget-table {
		height: calc(100% - 2.5rem);
		overflow: hidden scroll;
	}
}

@media screen and (min-width: utils.$tablet-min-width) {
	.__search,
	.__filter {
		flex: 1 1 50%;
		width: 50%;
	}
	.__search{
		flex: 1 1 45%;
		width:45%;
	}
	.__filter {
		flex: 1 1 55%;
		width: 55%;
	}
	.__head-wrap {
		flex-wrap: nowrap;
		margin-bottom: var(--half-space);
	}

	.__filter {
		margin: 0;
	}

	.__live-misc {
		height: 93%;
		flex-wrap: wrap;
		@include utils.flex-config(flex, null, null, null);
	}

	.__live-top-uncapped,
	.__live-top-overseas {
		flex: 1 0 100%;
		padding-bottom: 4rem;
	}

	.__completed,
	.__upcoming {
		/* .__head-row,
		.__table-row {
			grid-template-columns: repeat(auto-fit, minmax(4rem, 1fr));
		} */

		.__table-body {
			.__table-col {
				&:last-child {
					.__col-text {
						max-width: 20rem;
					}
				}
			}
		}
	}

	.__table-col {
		/* &:nth-child(2),
    &:last-child {
      grid-column: span 2;
    } */

		.__col-icon {
			position: relative;
			width: 3rem;
			height: 3rem;
			@include utils.flex-config(flex, null, center, center);

			&::before {
				position: absolute;
				content: "";
				width: 100%;
				height: 100%;
			}

			&.__icon-- {
				&sold {
					&::before {
						background: url("/static-assets/images/svg/icon-hammer-blue.svg")
							center center no-repeat;
					}
				}
				&unsold {
					&::before {
						background: url("/static-assets/images/svg/icon-hammer-red.svg")
							center center no-repeat;
					}
				}
				&overseas {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/icon-overseas.svg")
							center center no-repeat;
					}
				}
				&star {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/icon-star.svg") center
							center no-repeat;
					}
				}
				&skill-1 {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/skill-1.svg") center
							center no-repeat;
					}
				}
				&skill-2 {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/skill-2.svg") center
							center no-repeat;
					}
				}
				&skill-3 {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/skill-3.svg") center
							center no-repeat;
					}
				}
				&skill-4 {
					&::before {
						width: 1.5rem;
						height: 1.5rem;
						background: url("/static-assets/images/svg/skill-4.svg") center
							center no-repeat;
					}
				}
			}

			~ .__col-text {
				width: auto;
				padding-left: var(--half-space);
			}
		}
	}

	.__auction-modal {
		.__widget {
			width: 60%;
			height: calc(var(--window-inner-height) - 30rem);
		}
	}
}

@media screen and (min-width: utils.$large-desktop-min-width) {
	.__widget-full-vertical {
		height: calc(
			var(--window-inner-height) -
				(
					var(--header-height) + var(--current-bid-height) +
						var(--tab-btn-group-height) + 
						var(--full-space)
				)
		);
	}

	// .__widget-half-vertical {
	// 	height: calc(
	// 		(
	// 				var(--window-inner-height) -
	// 					(
	// 						var(--header-height) + var(--current-bid-height) +
	// 							var(--tab-btn-group-height) + var(--purse-height) +
	// 							var(--full-space)
	// 					)
	// 			) / 2
	// 	);
	// }

	.__super-data {
		// .__widget-super10 {
		// 	height: calc(
		// 		(
		// 				var(--window-inner-height) -
		// 					(
		// 						var(--header-height) + var(--current-bid-height) +
		// 							var(--tab-btn-group-height) + var(--full-space)
		// 					)
		// 			) - 22rem
		// 	);
		// }

		.__widget-super5 {
			height: 22rem;
		}
	}

	.__widget-table {
		// height: calc(100% - 2.5rem);
		overflow: hidden scroll;
	}

	.__filter-wrap {
		form {
			overflow: hidden;
		}

		.__form-group {
			flex: 1;
		}
	}

	.__table-body {
		@include utils.flex-config(flex, column, flex-start, center);
		.__table-row {
			max-height: 4rem;
			&.active{
				background: #ff00001c;
			}
		}
	}

	.__live-bids,
	.__live-top-buys{
		.__table-body {
			.__table-row {
				flex: 1 0 10%;
				// min-height: 10%;
				min-height:auto;
			}
		}
	}

	.__live-misc {
		.__table-body {
			.__table-row {
				flex: 1 0 20%;
				min-height: 20%;
			}
		}
		.__widget-body{
			height: 100%;
		}
	}

	.__live-top-buys {
		.__table-col {
			&:nth-child(1) {
				flex: 1 0 30%;
			}
			&:nth-child(2) {
				flex: 1 0 18%;
			}
			&:nth-child(3) {
				flex: 1 0 35%;
			}
			&:nth-child(4) {
