:root {
	// space variables
	--full-space: 2rem;
	--full-space-negative: calc(var(--full-space) * -1);
	--half-space: 1rem;
	--half-space-negative: calc(var(--half-space) * -1);
	--full-radius: 1rem;
	--half-radius: 0.5rem;

	//non-color variables
	--base-font-size: 62.5%;
	--container-max-width: 100%;
	--content-width: var(--window-inner-width);
	--vh: 1vh;
	--vw: 1vw;
	--window-inner-height: calc(var(--vh, 1vh) * 100);
	--window-inner-width: calc(var(--vw, 1vw) * 100);

	//color variables
	--color-red: hsl(0, 83%, 52%);
	--color-red-hsl: 0 83% 52%;
	--color-green: hsl(96, 99%, 34%);
	--color-green-hsl: 96 99% 34%;
	--color-light-green: hsl(96, 100%, 42%);
	--color-light-green-hsl: 96 100% 42%;
	--color-grey: hsl(0, 0%, 60%);
	--color-grey-hsl: 0 0% 60%;
	--color-light-grey: hsl(0, 0%, 78%);
	--color-light-grey-hsl: 0 0% 78%;
	--color-dark-blue: hsl(204, 100%, 15%);
	--color-dark-blue-hsl: 204 100% 15%;
	--color-black: hsl(0, 0%, 0%);
	--color-black-hsl: 0 0% 0%;
	--color-white: hsl(0, 0%, 100%);
	--color-white-hsl: 0 0% 100%;
	--color-page-background: hsl(228, 33%, 97%);

	//team color variables
	--team-1105-start: hsl(0, 0%, 36%);
	--team-1105-end: hsl(0, 0%, 11%);
	--team-1106-start: hsl(264, 45%, 22%);
	--team-1106-end: hsl(262, 55%, 32%);
	--team-1107-start: hsl(354, 87%, 45%);
	--team-1107-end: hsl(356, 74%, 29%);
	--team-1108-start: hsl(41, 100%, 60%);
	--team-1108-end: hsl(13, 96%, 56%);
	--team-1109-start: hsl(203, 100%, 34%);
	--team-1109-end: hsl(213, 100%, 28%);
	--team-1110-start: hsl(332, 85%, 49%);
	--team-1110-end: hsl(224, 65%, 16%);
	--team-1111-start: hsl(210, 100%, 22%);
	--team-1111-end: hsl(210, 100%, 22%);
	--team-1379-start: hsl(12, 95%, 57%);
	--team-1379-end: hsl(351, 91%, 51%);
	--team-2954-start: hsl(341, 69%, 39%);
	--team-2954-end: hsl(40, 100%, 60%);
	--team-2955-start: hsl(200, 6%, 10%);
	--team-2955-end: hsl(201, 100%, 19%);

	--team-3513-start: hsl(0, 0%, 36%);
	--team-3513-end: hsl(0, 0%, 11%);
	--team-3514-start: hsl(203, 100%, 34%);
	--team-3514-end: hsl(213, 100%, 28%);
	--team-3515-start: hsl(13, 96%, 56%);
	--team-3515-end: hsl(200, 6%, 10%);
	--team-3516-start: hsl(200, 6%, 10%);
	--team-3516-end: hsl(201, 100%, 19%);
	--team-3517-start: hsl(210, 100%, 22%);
	--team-3517-end: hsl(210, 100%, 22%);

	//predefined dimension variables
	--header-width: var(--window-inner-width);
	--header-height: 6.5rem;
	--logo-ratio: 0.75;
	--logo-width: 6rem;
	--logo-height: calc(var(--logo-width) * var(--logo-ratio));
	--upcoming-height: 12rem;
	--current-bid-height: 20rem;
	--tab-btn-group-height: 5.5rem;
	--purse-height: 3.5rem;

	//z-index variables
	--z-modal: 110;
	--z-header: 100;
	--z-table-header: 10;
	--z-head-icon: 20;
}

//min-width variables
$medium-mobile-min-width: 576px;
$tablet-min-width: 768px;

$desktop-min-width: 992px;
$desktop-max-width-992: 992px;
$desktop-min-width-1199: 1199px;
$desktop-max-width: 1199px;
$desktop-max-width-1280: 1280;
$large-desktop-min-width: 1200px;
$xl-desktop-max-width: 1600px;
$xl-desktop-min-width: 1600px;

//max-width variables
$mobile-max-width: 767.98px;
$tablet-max-width: 1179.98px;

$font-regular: "Roboto-Regular", sans-serif;
$font-italic: "Roboto-Italic", sans-serif;
$font-medium: "Roboto-Medium", sans-serif;
$font-mediumItalic: "Roboto-MediumItalic", sans-serif;
$font-bold: "Roboto-Bold", sans-serif;
$font-boldItalic: "Roboto-BoldItalic", sans-serif;

@media (min-width: $tablet-min-width) {
	:root {
		--container-max-width: 72rem;
	}
}

@media (min-width: $desktop-min-width) {
	:root {
		--container-max-width: 96rem;
	}
}

@media (min-width: $large-desktop-min-width) {
	:root {
		--container-max-width: 114rem;
		--current-bid-height: 12rem;
	}
}

@media (min-width: $xl-desktop-min-width) {
	:root {
		--container-max-width: 140rem;
	}
}
