	padding-left: var(--half-space);
	margin-right: auto;
	margin-left: auto;
}

.row {
	flex-wrap: wrap;
	margin-right: var(--half-space-negative);
	margin-left: var(--half-space-negative);
	@include mixins.flex-config(flex, null, null, null);
}

