	position: relative;
	width: 100%;
	padding-right: var(--half-space);
	padding-left: var(--half-space);
}

.embed-responsive {
	position: relative;
	display: block;
	width: 100%;
	padding: 0;
	overflow: hidden;

	& &-item,
	iframe,
	embed,
	object,
	video {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border: 0;
	}

	&::before {
		display: block;
		content: "";
	}

	&.embed-responsive- {
		&4by3 {
			&::before {
				padding-top: calc(3 / 4 * 100%);
			}
		}
		&16by9 {
			&::before {
				padding-top: calc(9 / 16 * 100%);
			}
		}
		&1by1 {
			&::before {
				padding-top: calc(1 / 1 * 100%);
			}
		}
		&2by3 {
			&::before {
				padding-top: calc(3 / 2 * 100%);
			}
		}
		&9by16 {
			&::before {
				padding-top: calc(16 / 9 * 100%);
			}
		}
		&21by9 {
			&::before {
				padding-top: calc(9 / 21 * 100%);
			}
		}
		&18by9 {
			&::before {
				padding-top: calc(9 / 18 * 100%);
			}
		}
		&18by6 {
			&::before {
				padding-top: calc(6 / 18 * 100%);
			}
		}
		&6by4 {
			&::before {
				padding-top: calc(4 / 6 * 100%);
			}
		}
	}
}

.table-responsive {
	display: block;
	width: 100%;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
}

