// @use 'variables';

@font-face {
	font-family: "Roboto-Regular";
	src: url("/static-assets/build/fonts/Roboto-Regular.eot");
	src: local("Roboto"), local("Roboto-Regular"),
		url("/static-assets/build/fonts/Roboto-Regular.eot?#iefix")
			format("embedded-opentype"),
		url("/static-assets/build/fonts/Roboto-Regular.woff2") format("woff2"),
		url("/static-assets/build/fonts/Roboto-Regular.woff") format("woff"),
		url("/static-assets/build/fonts/Roboto-Regular.ttf") format("truetype"),
		url("/static-assets/build/fonts/Roboto-Regular.svg#Roboto-Regular")
			format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

