@use "./utils";

.si-loginPage {
    background-color: var(--color-dark-blue);

    .si-loginCOver {
        @include utils.flex-config(flex, row, center, center);
    }

    .si-LeftPart,
    .si-RightPart {
        width: 50%;
        flex: 0 0 50%;
        @include utils.flex-config(flex, row, center, center);
    }

    .si-toolImg {
        width: 58vh;
        height: 58vh;
        object-fit: cover;

        & img {
            width: 100%;
            height: 100%;
        }
    }
    .si-RightPart{
   
        .si-Loginform{
        background:#fff;
        width: 64rem;
        height: 40rem;
        margin-right: -6vw;
        border-radius: 3vw;
        padding-left: 6vw;
        padding-top: 2vw;
        // margin-top: 3vw;

       .si-Title{
        color:#002F4F;
        text-transform:uppercase;
        padding-bottom: 2vw;
       }
        .si-top{
            font: 2.7rem utils.$font-bold;
            
        }
        .si-bottom{
            font: 2rem utils.$font-regular;
            padding-top: 0.2rem;
        }
        .si-userName ,.si-password{
            @include utils.flex-config(flex, column, flex-start, flex-start);
        }
        .si-password{
            margin-top: 1.5vw;
        }
        .si-signIn{
            width: 25rem;
            height: 4rem;
            background:#EA1D1D;
            box-shadow: 0px 4px 10px #515151;
            border-radius: var(--full-radius);
            font: 1.6rem utils.$font-medium;
            color:#fff;
            text-align:center;
            margin-top:1vw;
            line-height:4rem;
            cursor: pointer;
        }
        .si-label{
            font: 1.4rem utils.$font-medium;
            text-transform:uppercase;
            color:#002F4F;
            padding-bottom: 1rem;
        }
        input{
            border: 1px solid #002F4F;
            filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.25));
            width: 25rem;
            height: 4rem;
            padding:0 1rem;
            border-radius: var(--full-radius);
            font: 1.6rem utils.$font-medium;
        }
        ::-webkit-input-placeholder { /* Edge */
            color: #002F4F;
          }
          
          :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #002F4F;
          }
          
          ::placeholder {
            color: #002F4F;
          }
        }
    }
}